// Create a styled link to another report
var DetailReportLink = React.createClass({
    urlReplace: function (urlFormat, id) {
        var idForUrl = encodeURIComponent(id);
        return urlFormat.replace(/{ID}/, idForUrl);
    },
    render: function () {
        return <a className="detailLink" target="_blank" href={this.urlReplace(this.props.url, this.props.id)} title={this.props.title}><span className="icon-open-new-windows" /></a>;
    }
});

// Creates a mailto link for the given 'to' address and optional subject, safely escaping and stripping highlight
var MailLink = React.createClass({
    render: function () {
        var mailToUrl = "mailto:" + encodeURIComponent(stripHighlight(this.props.to));
        var parameters = {
            subject: stripHighlight(this.props.subject),
            body: window.location.href
        };
        mailToUrl += buildUrlParameters(parameters);

        var content = <a href={mailToUrl} title={this.props.columnName + ':' + stripHighlight(this.props.to)} dangerouslySetInnerHTML={highlight(this.props.to)} />;
        if (this.props.bold !== "false") content = <b>{content}</b>;

        var label = null;
        if (this.props.label) label = <span>{this.props.label + ": "}</span>

        return <div>{label} {content}</div>;
    }
});

// Writes out a field with label and highlighting, but only if the value is set
var DetailsField = React.createClass({
    render: function () {
        if (!this.props.value || this.props.value === "N/A" || this.props.value === "Unknown") return null;

        var link = null;
        if(this.props.url) link = <DetailReportLink url={this.props.url} id={this.props.id} title={this.props["url-title"]} />;

        var content = <span dangerouslySetInnerHTML={highlight(this.props.value)} title={this.props.columnName + ':' + stripHighlight(this.props.value)} />;
        if (this.props.bold) content = <b>{content}</b>;

        var label = null;
        if (this.props.label) label = <span>{this.props.label + ": "}</span>;

        if (this.props.inline === "true") {
            return <span>{label} {content}{link}</span>
        } else {
            return <div>{label} {content}{link}</div>
        }
    }
});

// RiskMarker emits an inline value with a red exclamation point if the given condition is
var RiskMarker = React.createClass({
    render: function () {
        if (!this.props.condition) return null;
        return <div><span className="error-icon icon" title={this.props.columnName} /><b>{this.props.label}</b></div>;
    }
});

// RiskNumber emits a number which is red if not zero
var BadUnlessZero = React.createClass({
    render: function () {
        if (!this.props.value || this.props.value.toString() === "0") return <span title={this.props.columnName + ':0' }>0</span>;
        return <b title={this.props.columnName + ':' + this.props.value }>{this.props.value}</b>;
    }
});

// RelativeDateTime writes a DateTime in terms of now (ex: 3 days ago), and highlights it yellow if over 2.5 days and red if over 6.5 days.
var RelativeDateTime = React.createClass({
    render: function () {
        var label = null;
        if (this.props.label) label = <span>{this.props.label + " "}</span>;

        if (!this.props.value) {
            return <div><span className="error-icon icon" />{label} Unknown</div>
        }

        var dateTime = moment(this.props.value);
        var howLongSince = moment.duration(moment().diff(dateTime));

        var prefix = null;
        if (howLongSince.asDays() > 6.5) {
            prefix = <span className="error-icon icon" />;
        } else if (howLongSince.asDays() > 2.5) {
            prefix = <span className="warn-icon icon" />;
        }

        var content = <span>{dateTime.fromNow()}</span>;
        if(prefix) content = <b>{dateTime.fromNow()}</b>;

        return <div title={this.props.columnName + ':' + this.props.value}>{prefix}{label}{content}</div>;
    }
});